<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left" >
            <div id="divRequestionItemsData" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-4 float-left">
                <div class="heading-border">
                    <h5 class="h5">
                        <span>{{ $t('ListOfAssest') }}</span>
                    </h5>
                </div>
                <div class="row" >
                    <div class="col-12 mb-2 mt-2">
                        <div class="mr-2 d-inline-block">
                            <span class="legend-Progress colorboxsquare"></span>{{ $t('ContainerProducts') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="legend-ovrdue colorboxsquare"></span>{{ $t('NotAvailable') }}
                        </div>
                        <div class="mr-2 d-inline-block">
                            <span class="text-danger font-weight-bold">*</span>{{ $t('MandatoryItems') }}
                        </div>
                    </div>
                </div>
                <div class="row"  >
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left listing">
                        <div class="table-responsive tableborder">
                            <table id="tblassestassignmentinfo" class="table table-bordered dt-responsive nowrap my-0">
                                <thead class="thead-bg">
                                    <tr>
                                        <th style="min-width:50px;" class="text-center"><span>{{ $t('hash') }}</span></th>
                                        <th style="min-width:50px;" class="text-center"><span>{{ $t('ItemType') }}</span>
                                        </th>
                                        <th style="min-width:50px;" class="text-center"><span>{{ $t('ItemCode') }}</span>
                                        </th>
                                        <th style="min-width:50px;" class="text-center"><span> {{ $t('ItemName') }}</span>
                                        </th>
                                        <th style="min-width:50px;" class="text-center"><span> {{ $t('LocationName')
                                        }}</span></th>
                                        <th style="min-width:285px;"><span> {{ $t('UniqueName') }}</span></th>
                                        <th style="min-width:50px;" class="text-center"><span> {{ $t('TermsAndConditions')
                                        }}</span></th>
                                        <th style="min-width:50px;" class="text-center"><span> {{ $t('AvailableItems')
                                        }}</span></th>
                                        <th style="min-width:50px;" class="text-center"><span> {{
                                            $t('PendingAvailableQuantity') }}</span></th>
                                        <th style="min-width:150px;" class="text-center"><span>{{ $t('RequestQuantity') }}
                                            </span></th>
                                        <th style="min-width:200px;" class="text-center"><span>{{ $t('Compartment')
                                        }}</span></th>
