                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <template v-for="(item, index) in AssetListData">

                                        <tr  class="parentmain insertedrow bg-gold showassetcataloguegroup"
                                            :class="item.clsColor">
                                            <td>
                                                <span v-if="item.AssetCatalogueGroup.length > 0"  v-on:click="collapseExpandChild(index)"
