                                                    checked="checked" class="assertermscondition" />

                                                <span v-else>{{ $t('NA') }}</span>

                                            </td>
                                            <td class="text-center">{{ item.AvailableItems }}</td>
                                            <td class="text-center">{{ item.PendingAvailableQuantity }}</td>
                                            <td class="text-center">

                                                <Field name="Requested Quantity" :rules="{ 'required': true }" v-slot="{ errors }">
                                                    <input @keyup="checkQuantity(item)" v-on:input="checkQuantity(item)" inputmode="numeric"   v-model="item.RequestQuantity" type="number" min="1" value="1" maxlength="8" v-bind:class="{ 'txtitems form-control txtAssetQuentity': true, 'is-invalid': errors[0] }" />
                                                </Field>
                                                <!-- <input type="text" class="txtitems form-control txtAssetQuentity"
                                                    :class="item.clsQtyDisabled" v-on:input="RequestedQuantity(item)" :min="1" v-model="item.RequestQuantity" /> -->
                                            </td>
                                            <td class="text-center">
                                                <select
                                                    v-if="item.ContainerList != null && item.ContainerList.Count > 0 && !(item.ContainerList.Text == '' || item.ContainerList.Text == null)"
                                                    class="disabled form-control" readonly="readonly">
                                                    <option>{{ item.ContainerList.Text }}</option>
                                                </select>
                                                <span v-else>{{ $t('NA') }}</span>
                                            </td>
                                            <td id="lastrow1" class="text-center">
                                                <a v-if="item.HasRelatedProducts" href="javascript:;" class="relatedproduct mr-2"
                                                    @click="relatedproduct(item)" title="Relateditem"><em
                                                        class="fa fa-sitemap text-primary" aria-hidden="true"></em></a>
                                              
                                                    <a @click="deleteItem(index)" 
                                                    title="Remove" href="javascript:;" class=" recyclebin enable"><i class="fa fa-trash text-danger action-icon"></i></a>

                                            </td>
                                        </tr>
                                        <tr class="d-none" :id="'child_'+index" v-for="(child, cindex) in item.AssetCatalogueGroup" :key="cindex"
                                            :class="child.clsColor">
                                            <td style="">
                                                <span id="cataloguegroup_id" class='hidden showcatalog'></span>
                                            </td>
                                            <td class="text-center">{{ child.AssetType }}</td>
                                            <td class="text-center">{{ child.ItemCode }}</td>
                                            <td class="text-center item-name">{{ child.AssetCatalogName }}</td>
                                            <td class="text-center">{{ child.LocationName }}</td>
                                            <td class="text-center">
                                                <input type="text" name="txtcsUniqueName"
                                                    class="form-control txtcsUniqueName" v-model="child.AssetUniqueName" />
                                                <template v-if="item.ParentUniqueId <= 0">
                                                    <div class="d-block py-2 text-center"> {{ $t('OR') }}</div>                                                   
                                                    <input type="text" v-model="child.searchBarcode"
                                                        class="form-control txtBarcodesearch col-10 text-center float-left"
                                                        placeholder="Enter Barcode" />
                                                    <a href="javascript:;" class="float-left green ancsearchbrcode m-2 "
                                                        title="ClickHereToSearch">
                                                        <i
                                                            class="searchicon enable fa fa-search action-icon text-success"></i>
                                                    </a>
                                                </template>
                                            </td>
                                            <td class="text-center">
                                                <input v-if="item.IsEnableTerms == 1" type="checkbox" value="1"
                                                    disabled="disabled" checked="checked" class="assertermscondition" />
                                                <span v-else>{{ $t('NA') }}</span>
                                            </td>
                                            <td class="text-center">{{ child.AvailableItems }}</td>
                                            <td class="text-center">{{ child.PendingAvailableQuantity }}</td>
                                            <td class="text-center">                                                                                               
                                                <!-- <input type="text" :min="1" class="txtitems form-control txtAssetQuentity" :disabled="clsQtyDisabled" v-on:input="RequestedQuantity(item.RequestQuantity)" v-model="RequestQuantity" /> -->

                                                <Field name="Requested Quantity" :rules="{ 'required': true }" v-slot="{ errors }">
                                                    <input @keyup="checkQuantity(item)" @input="checkQuantity(item)" inputmode="numeric" :disabled="child.clsQtyDisabled"  v-model="child.RequestQuantity" type="number" min="1" value="1" maxlength="8" v-bind:class="{ 'txtitems form-control txtAssetQuentity': true, 'is-invalid': errors[0] }" />
                                                </Field>
                                            </td>
                                            <td class="text-center">
                                                <select class="disabled form-control" readonly="readonly"
                                                    v-if="child.ContainerList != null && child.ContainerList.Count > 0 && !(child.ContainerList.Text == '' || child.ContainerList.Text == null)">
                                                    <option>{{ child.ContainerList.Text }}</option>
                                                </select>
                                                <span v-else>{{ $t('NA') }} </span>
                                            </td>
                                            <td id="lastrow1" class="text-center">
                                                <a v-if="child.HasRelatedProducts" href="javascript:;"
                                                    class="relatedproduct mr-2" @click="relatedproduct(child)"
                                                    title="Relateditem"><em class="fa fa-sitemap text-primary"
                                                        aria-hidden="true"></em></a>
                                                <a href="javascript:;" class="recyclebin enable" title="Remove"><i class="fa fa-trash text-danger action-icon"></i></a>

                                              
                                            </td>
                                        </tr>
                                    </template>

                                    <template id="fromPendingList" v-for="(item,index) in GetRequstionList">
                                        <tr  class="parentmain insertedrow bg-gold showassetcataloguegroup"
                                            :class="item.clsColor">
                                            <td>                                               
                                                    <span v-if="item.isParentChildEqualAndChildGreaterThenZero>0"  class='togglearrow arrowwhiteexpand_roles showcatalog'></span>
                                                    <span v-else  class='togglearrow arrowwhiteexpand_roles  showcatalog hidden'></span>
                                             
                                            </td>
                                            <td class="text-center">{{ item.AssetType }}</td>
                                            <td class="text-center">{{ item.ItemCode }}</td>
                                            <td class="text-center item-name">{{ item.CatalogName }}</td>
                                            <td class="text-center">{{ item.LocationName }}</td>
                                            <td class="text-center" v-if="item.ChildUniqueId <= 0">
                                               
                                                    <div  class="d-block py-2 text-center">OR</div>
                                                    <div class="clearfix"></div>
                                                    <input type="text" class="form-control txtBarcodesearch col-10 text-center float-left" v-on:click="SetUniqueNameData(item)" v-model="item.searchBarcode" placeholder="Enter Barcode" />
                                                    <a href="javascript:;" class="float-left green ancsearchbrcode m-2 ">
                                                        <i class="searchicon enable fa fa-search action-icon text-success"></i>
                                                    </a>
                                                
                                            </td>
                                            <td v-else>
                                                <input type="text" name="txtcsUniqueName"  class="form-control txtcsUniqueName"   v-model="item.UniqueName" />
                                                                        
                                            </td>
                                            <td class="text-center">
                                            
                                                <input v-if="item.IsEnableTerms == 1" type="checkbox" value="1"
                                                    disabled="disabled"
                                                    checked="checked" class="assertermscondition" />

                                                <span v-else>{{ $t('NA') }}</span>

                                            </td>
                                            <td class="text-center">{{ item.AvailableItems }}</td>
                                            <td class="text-center">{{ item.HasUniqueNames == true?1:item.RequestQuantity }}</td>
                                            <td class="text-center" v-if="item.HasUniqueNames==true || (item.HasUniqueNames==false && item.ChildUniqueId >0)">

                                                <input type="text"  maxlength="4" data-numberonly="yes" onkeyup="chkItems(this)" v-model="item.RequestQuantity" class="txtitems form-control" />


                                                    <input type="hidden" name="assignedId" id="Assigned_hdn" value="" />


                                                    <span></span>
                                              
                                            </td>
                                            <td class="text-center" v-else>
                                              
                                                <input type="text"  maxlength="4" data-numberonly="yes" onkeyup="chkItems(this)" v-model="item.RequestQuantity" v-bind:class="{'txtitems form-control':true,'disabled':item.HasUniqueNames == true}" />


                                                <input type="hidden" name="assignedId" id="Assigned_hdn" value="" />


                                                <span></span>
                                            </td>
                                            <td class="text-center" v-if="item.ContainerList != null && item.ContainerList.Text== false">

                                                <select class="disabled form-control" readonly="readonly">
                                                    <option>{{item.ContainerList.Text}}</option>
                                                </select>
                                            </td>
                                            <td class="text-center" v-else>
                                                <span>NA</span>
                                            </td>
                                            <td class="text-center">
                                           
                                                <a  v-if="item.HasUniqueNames == false && item.ChildUniqueId <= 0" href="javascript:;" class="ancaddmoredata  addchild enable"><i class="fa fa-plus-circle text-info action-icon"></i></a>
                                            
                                            <a href="javascript:;" v-on:click="DeleteRecordFromPendingList(index)" class="ancremoveData  recyclebin  enable"  title="Remove"><i class="fa fa-trash text-danger action-icon"></i></a>
                                        </td>
                                        </tr>


                                        <tr  v-for="(child,index1) in item.childList"  class="parentmain insertedrow bg-gold showassetcataloguegroup"
                                            :class="child.clsColor">
                                            <td>                                               
                                                    <span  class="togglearrow arrowwhiteexpand_roles showcatalog"></span>
                                             
                                            </td>
                                            <td class="text-center">{{ child.AssetType }}</td>
                                            <td class="text-center">{{ child.ItemCode }}</td>
                                            <td class="text-center item-name">{{ child.CatalogName }}</td>
                                            <td class="text-center">{{ child.LocationName }}</td>
                                            <td class="text-center" v-if="child.ChildUniqueId <= 0">
                                               
                                                    <div  class="d-block py-2 text-center">OR</div>
                                                    <div class="clearfix"></div>
                                                    <input type="text" class="form-control txtBarcodesearch col-10 text-center float-left" v-on:click="SetUniqueNameData(child)" v-model="child.searchBarcode"  placeholder="Enter Barcode" />
                                                    <a href="javascript:;" class="float-left green ancsearchbrcode m-2 ">
                                                        <i class="searchicon enable fa fa-search action-icon text-success"></i>
                                                    </a>
                                                
                                            </td>
                                            <td v-else>
                                                <input type="text" name="txtcsUniqueName"  class="form-control txtcsUniqueName"   v-model="child.UniqueName" />
                                                                        
                                            </td>
                                            <td class="text-center">

                                                <input v-if="child.IsEnableTerms == 1" type="checkbox" value="1"
                                                    disabled="disabled"
                                                    checked="checked" class="assertermscondition" />

                                                <span v-else>{{ $t('NA') }}</span>

                                            </td>
                                            <td class="text-center">{{ child.AvailableItems }}</td>
                                            <td class="text-center">{{ child.HasUniqueNames == true?1:child.RequestQuantity }}</td>
                                            <td class="text-center" v-if="child.HasUniqueNames==true || (child.HasUniqueNames==false && child.ChildUniqueId >0)">

                                                <input type="text"  maxlength="4" data-numberonly="yes" onkeyup="chkItems(this)" v-model="child.RequestQuantity" class="txtitems form-control" />


                                                    <input type="hidden" name="assignedId" id="Assigned_hdn" value="" />


                                                    <span></span>
                                              
                                            </td>
                                            <td class="text-center" v-else>
                                              
                                                <input type="text"  maxlength="4" data-numberonly="yes" onkeyup="chkItems(this)" v-model="child.RequestQuantity" v-bind:class="{'txtitems form-control':true,'disabled':item.HasUniqueNames == true}" />


                                                <input type="hidden" name="assignedId" id="Assigned_hdn" value="" />


                                                <span></span>
                                            </td>
                                            <td class="text-center" v-if="child.ContainerList != null && child.ContainerList.Text== false">

                                                <select class="disabled form-control" readonly="readonly">
                                                    <option>{{child.ContainerList.Text}}</option>
                                                </select>
                                            </td>
                                            <td class="text-center" v-else>
                                                <span>NA</span>
                                            </td>
                                            <td class="text-center">
                                           
                                                <a  v-if="child.HasUniqueNames == false && child.ChildUniqueId <= 0" href="javascript:;" class="ancaddmoredata  addchild enable"><i class="fa fa-plus-circle text-info action-icon"></i></a>
                                            
                                            <a href="javascript:;" v-on:click="DeleteRecordFromPendingList(index1)" class="ancremoveData  recyclebin  enable"  title="Remove"><i class="fa fa-trash text-danger action-icon"></i></a>
                                        </td>
                                        </tr>
                                        
                                    </template>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <RelatedProrduct v-if="Isrelatedproduct" @CloseRelatedPopup="CloseRelatedPopup" 
            :RelatedProductDaata="RelatedProductDaata"  :AssetListData="AssetListData" @RelatedProduct="RelatedProduct"></RelatedProrduct>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
import RelatedProrduct from '../../components/Modal/RelatedProductPopup.vue'
export default {
    components: { RelatedProrduct },
    props: {
        ListData: {
            type: Array,
            required: true
        },
        issueTo: {
            type: Number,
            required: true

        }
    },
    data() {
        return {
            isLoading:false,
            AssetListData: [],
            GetRequstionList:[],
            clsColor: '',
            clsQtyDisabled: '',
            UserId: null,
            RequestQuantity: 1,
            AssestArray: [],
            Isrelatedproduct: false,
            RelatedProductDaata: [],
            uniqueIds:[],
            indexGetRequstionList:-1
        }
    },    
    created: function () {   
        //debugger 
        if(this.$parent.isListFromPendingList)  {
            this.GetDataFromPendingRequest(this.ListData);
            this.$parent.isListFromPendingList=false;
        }   
        else{
            this.GetCompanyAssestCatalogs();
        }
       
        this.UserId = this.GetUserInfo.ID;
    },    
    methods: {
        RelatedProduct:function(ths){            
            this.AssetListData.push(ths[0]);            
        },
        SetUniqueNameData:function(itm){
            var vm=this;

            //debugger       
           var chkData = true;
            barcode = itm.searchBarcode;
       
       
        },
        collapseExpandChild:function(index){
            //debugger
                if($('#child_'+index).hasClass('d-none')){
                    $('#child_'+index).removeClass('d-none');
                }
                else{
                    $('#child_'+index).addClass('d-none');
                }
        },
        GetCompanyAssestCatalogs: function () { 
        //debugger                   
            this.isLoading=true;
            var url = `id=${this.ListData[0].AssetCatalogueId}&isView=1&barcode=&assetType=${this.ListData[0].AssetTypeId}&assetUniqueId=${this.ListData[0].ParentUniqueId}&selfCheckout=no&reqFrom=assetassignment&isFor=search&isuniqueSearch=1&issueTo=${this.issueTo}&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${this.ListData[0].LocationId}&RFID=0&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=${this.ListData[0].ParentUniqueId}&catalogueIds=${this.ListData[0].AssetCatalogueId}`;
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
               // this.AssetListData = response.data;
               response.data.forEach(newItem => {                   
                    // Check if AssetCatalogUniqueId is not in vm.AssetListData                  
                    response.data.forEach(newItem => {                       
                        // Check if the identifier is not in uniqueIds array
                        const identifier = JSON.stringify(newItem.ParentUniqueId); // Convert the object to a string for comparison
                        if (!this.uniqueIds.includes(identifier)) {
                            // Add the identifier to the array
                            this.uniqueIds.push(identifier);
                            // Add the entire response item to this.AssetListData
                            this.AssetListData.push(newItem);
                        }
                    });
                    this.isLoading=false;
                });                
                this.AssetListData.forEach(ele => {
                    ele.RequestQuantity=1;
                    ele.clsColor="";
                    ele.searchBarcode='';
                    ele.clsQtyDisabled ="";
                    if( ele.CreateUniqueNameListing && ele.ParentUniqueId > 0){
                        ele.clsQtyDisabled="disabled";
                    }
                    if (ele.IsContainer == 1) {
                        ele.clsColor = "low-bar";
                    }
                    this.clsQtyDisabled = ele.CreateUniqueNameListing && ele.ParentUniqueId > 0 ? "disabled" : "";
                    ele.AssetCatalogueGroup.forEach(child => {
                        child.clsColor = "";
                        child.searchBarcode='';
                        child.clsQtyDisabled ="";
                        child.RequestQuantity=1;
                            if( child.CreateUniqueNameListing && child.ParentUniqueId > 0){
                                child.clsQtyDisabled="disabled";
                            }
                        if (child.CreateUniqueNameListing == true) {
                            child.AvailableItems = 1;
                            child.AsseetGroupQuantity = 1;
                        }
                        if (child.IsContainer == 1) {
                            child.clsColor = "low-bar";
                        }
                    });
                });
                this.GetDataFromAsset();
                this.isLoading=false;
            });
        },
       GetDataFromPendingRequest:function(data){
        //debugger
                   
                var vm= this;

         data.GetRequstionList.forEach(ele => {
			
			ele.clsColor="";
			if (ele.Information.IsContainer == 1) {
				ele.clsColor = "low-bar";
			}
			 ele.isParentChildEqualAndChildGreaterThenZero= data.GetRequstionList.filter(c => c.ParentUniqueId == ele.ChildUniqueId && ele.ChildUniqueId > 0).length;
			 ele.childList= data.GetRequstionList.filter(x => x.ParentUniqueId == ele.ChildUniqueId && ele.ChildUniqueId > 0)

			ele.childList.forEach(ele1 => {
                ele1.searchBarcode='';
                ele1.clsColor="";
                if (ele1.IsContainer == 1) {
                    ele1.clsColor = "low-bar";
                }
			})

			ele.searchBarcode='';
		  //debugger
		   vm.GetRequstionList.push(ele);
           
	});

           
       },
        checkQuantity(item){
            var vm = this;
            let TotalQuantity = item.AvailableItems;          
            if (item.RequestQuantity > item.AvailableItems) {
                vm.AssetListData.forEach(ele => {                    
                    if (ele.AssetCatalogueId == item.AssetCatalogueId) {
                        ele.RequestQuantity=ele.AvailableItems;
                        item.RequestQuantity=item.AvailableItems;
                        vm.ShowAlert(vm.$t('RequestQuanitityNotMoreThen')+' '+TotalQuantity, "warning", true, vm.$t("Alert"));                                            
                    }
                })
            }        
            this.GetDataFromAsset();
        },     
        DeleteRecordFromPendingList(index){
            var vm=this;
           // debugger
            vm.indexGetRequstionList=index;
            vm.confirmR(vm.$t('Please verify that you want to delete this record'), true, false, function (index) {
                   
                    vm.GetRequstionList.splice( vm.indexGetRequstionList, 1);
                    vm.indexGetRequstionList=-1;
                return vm.ShowAlert(
                    vm.$t("Product has been deleted Successfully "),
                    "success",
                    vm.$t("Alert")                   
                );              
            });        
        },      
        GetDataFromAsset() {                    
            var vm = this;
            vm.AssetListData.forEach(item => {
                // Check if AssetCatalogUniqueId is not in vm.AssestArray
                if (!vm.AssestArray.some(asset => asset.AssetCatalogUniqueId === item.ParentUniqueId)) {
                    let obj = {
                        AssetCatalogUniqueId: item.ParentUniqueId,
                        RequestFromRequsition: 1,
                        AssetQuantity: parseInt(item.RequestQuantity),
                        RequestQuantity: parseInt(item.RequestQuantity),
                        BatchId: '',
                        AssetUniqueName: item.Name,
                        IsGroup: null,
                        ParentAssetCatalogUniqueId: item.AssetCatalogueId
                    };                    
                    vm.AssestArray.push(obj);
                }else if(!vm.AssestArray.some(asset => asset.RequestQuantity === item.RequestQuantity) && vm.AssestArray.some(asset => asset.AssetCatalogUniqueId === item.ParentUniqueId)){
                    const indexToRemove = vm.AssestArray.findIndex(asset => asset.AssetCatalogUniqueId === item.ParentUniqueId);                   
                    if (indexToRemove !== -1) {
                        vm.AssestArray.splice(indexToRemove, 1);
                    }
                    let obj = {
                        AssetCatalogUniqueId: item.ParentUniqueId,
                        RequestFromRequsition: 1,
                        AssetQuantity: parseInt(item.RequestQuantity),
                        RequestQuantity: parseInt(item.RequestQuantity),
                        BatchId: '',
                        AssetUniqueName: item.Name,
                        IsGroup: null,
                        ParentAssetCatalogUniqueId: item.AssetCatalogueId
                    };                    
                    vm.AssestArray.push(obj);
                }
            });
            vm.$emit('AssestArray', this.AssestArray);            
        },
        relatedproduct: function (ths) {
    
            this.Isrelatedproduct = true;
            this.RelatedProductDaata.push(ths);
        },
        CloseRelatedPopup: function () {
            this.Isrelatedproduct = false;
        },

        deleteItem: function (index) {           
            var vm = this;
            //debugger
            vm.confirmR(vm.$t('Please verify that you want to delete this record'), true, false, function (result) {
               
           vm.AssetListData.splice(index, 1);
                return vm.ShowAlert(
                    vm.$t("Product has been deleted Successfully "),
                    "success",
                    vm.$t("Alert")
                   
                ); 
            });  
           
        },
        

    }
}
</script>